<template>
	<div>
		<el-row>
			<el-table
				ref="table"
				:data="showType=='all'?items:selectedItems"
				border
				stripe
				class="w tc"
				:row-key="getRowKeys"
				@selection-change="selectionChange"
				:header-cell-style="{ 'text-align': 'center' }"
				:size="size"
				:max-height="maxHeight"
				v-loading="loading"
				:element-loading-text="loadingOption.text"
				:element-loading-spinner="loadingOption.spinner"
				:element-loading-background="loadingOption.background"
				:show-summary="showSummary"
				:summary-method="getSummaries"
			>
				<!---->
				<el-table-column
					v-if="selections"
					type="selection"
					width="55"
					:selectable="selectable"
					align="center"
					:reserve-selection="true"
				></el-table-column>
				<!---->
				<el-table-column
					label=""
					width="65"
					type="index"
					align="center"
					v-if="isSort"
				></el-table-column>
				<!-- <el-table-column label="" width="65" align="center" v-if="isSort">
                        <template slot-scope="scope">
                            <span>{{ getIndex(scope.$index+1) }}</span>
                        </template>
                    </el-table-column> -->
				<!---->
				<template v-for="column in columns">
					<!---->
					<table-column
						v-if="column.children && column.children.length"
						:key="column.columnName"
						:coloumn-header="column"
					></table-column>
					<!---->
					<el-table-column
						v-else
						:prop="column.prop"
						:key="column.columnName"
						:width="column.width"
						:label="column.label"
						:min-width="column.minWidth"
						:align="column.align"
						:fixed="column.fixed"
					>
						<template slot-scope="scope">
							<my-render
								v-if="column.render"
								:row="scope.row"
								:render="column.render"
							></my-render>
							<span v-else>{{ scope.row[column.prop] }}</span>
						</template>
					</el-table-column>
				</template>
			</el-table>
		</el-row>
		<el-row>
			<el-pagination
				v-if="pagination && showType=='all'"
				class="mt10 tr"
				:current-page="page"
				:page-size="pageSize"
				:total="total"
				@current-change="currentChange"
				@size-change="sizeChange"
				background
				layout="prev, pager, next, jumper, slot"
			>
				<span class="fl"
					>{{ total }},{{ page }}/{{ totalPage }}</span
				>
			</el-pagination>
			<span v-else class="fl mt10 el-pagination" style="line-height: 28px"
				>{{ total }}</span
			>
		</el-row>
	</div>
</template>

<script>
import MyRender from './MyRender'
import TableColumn from './TableColumn'
import columnsAll from './columnsAll.js'
import { getIndex } from '@/libs/utils'
export default {
	name: 'MyTable',
	components: {
		MyRender,
		TableColumn,
	},
	props: {
		// 
		outerData: { type: Array, default: () => [] },
		// 
		fetchFun: Function,
        outParams: { type: Object, default: () => {} },
        // ,this.$parentbug
        parent: Object,
		// 
		columnNames: { type: Array, default: () => [] },
		disableItems: { type: Array, default: () => [] },
		// 
        selections: { type: Boolean, default: false },
        showType: {type: String, default: 'all'},
		// 
		pagination: { type: Boolean, default: true },
		// 
		showSummary: { type: Boolean, default: false },
		getSummaries: Function,
		//
		loadingOption: {
			type: Object,
			default: function() {
				return {
					text: '',
					spinner: '',
					background: '',
				}
			},
		},
		rowKey: { type: String, default: '' },
		maxHeight: Number,
		isSort: {
			type: Boolean,
			default: true,
		},
		size: {
			type: String,
			default: 'mini',
		},
	},
	data() {
		return {
			loading: false,
			page: 1,
            pageSize: 15,
			items: [],
			selectedItems: [],
			total: 0,
		}
	},
	computed: {
		totalPage: function() {
			return Math.ceil(this.total / this.pageSize || 1)
		},
		columns() {
			return this.getColumns(this.columnNames)
		},
	},
	mounted() {
		this.initData()
	},
	methods: {
		/* 
		 * colsName 
		 */
		getColumns(columnNames = []) {
			// 
			let colsAll = columnsAll(this.parent || this.$parent)
			let cols = []
			for (let i = 0, leni = columnNames.length; i < leni; i++) {
				for (let j = 0, lenj = colsAll.length; j < lenj; j++) {
					if (columnNames[i] === colsAll[j].columnName) {
						cols.push(colsAll[j])
						break
					}
				}
			}
			return cols
		},
		// 
		selectable(row) {
			let flag = true
			this.disableItems.map((item) => {
				if (item.wareId === row.wareId || item.wareId === 0) {
					flag = false
				}
				return
			})
			return flag
		},
		// 
		initData() {
			// 
			if (this.outerData.length) {
				let start = (this.page - 1) * this.pageSize
				let end = this.page * this.pageSize
				this.items = this.outerData.slice(start, end)
				this.total = this.outerData.length
			} else {
				// 
				this.handleFetch()
			}
		},
		// 
		async handleFetch() {
			this.loading = true
			const params = this.pagination
				? Object.assign(
                    {},
                    {
                        page: this.page,
                        pageSize: this.pageSize,
                    },
                    this.outParams
				)
				: this.outParams
			// 
			params && (this.pageSize = params.pageSize)
			const res = await this.fetchFun(params)
			this.loading = false
			if (!res) return // resundefined
			// const {data:{data}} = res;
			const { data } = res
			// 
			if (!data) {
				this.total = 0
				this.items = []
				return
			}
			// 
			if (this.pagination) {
				this.total = data.total
				this.items = data.data
			} else {
				this.total = data.length
				this.items = data
			}
		},
		// 
		selectionChange(val) {
            this.selectedItems = val
            this.$emit("selectedDataChange", val);
		},
		// 
		currentChange(val) {
			if (this.loading) {
				return
			}
			this.page = val
			this.initData()
		},
		// 
		handlePreviousPage(noPreviousPage) {
			if (this.loading) {
				return
			}
			if (this.page === 1) {
				return noPreviousPage()
			}
			this.page--
			this.initData()
		},
		// 
		handleNextPage(noNextPage) {
			if (this.loading) {
				return
			}
			if (this.total <= this.page * this.pageSize) {
				noNextPage()
				return
			}
			this.page++
			this.initData()
		},
		// 
		sizeChange(val) {
			this.pageSize = val
			this.page = 1
			this.initData()
		},
		getIndex(idx) {
			return getIndex(idx)
		},
		getRowKeys(row) {
			return row[this.rowKey]
		},
	},
}
</script>

<style lang="scss" scoped></style>
