<template>
    <div>
        <el-dialog :title="title" :visible.sync="visible" :show-close='showClose' :close-on-click-modal='clickOutClose'
            :close-on-press-escape='clickEscClose' :center='centerLayout' :before-close="beforeClose">
            <slot name="body"></slot>
            <span slot="footer" class="dialog-footer">
                <el-button @click="visible = false" v-if="showCancel"></el-button>
                <el-button type="primary" @click="onSub">{{subText}}</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: 'MyDialog',
        data() {
            return {
                visible: false
            }
        },
        props: {
            title: String,
            showCancel: { //
                type: Boolean,
                default: true
            },
            subText: { //
                type: String,
                default: ''
            },
            showClose: { //
                type: Boolean,
                default: true
            },
            clickOutClose: { //
                type: Boolean,
                default: true
            },
            clickEscClose: {//esc
                type: Boolean,
                default: true
            },
            centerLayout:{//
                 type: Boolean,
                default: false
            }
        },
        methods: {
            showDialog() {
                this.visible = true;
            },
            onSub() {
                this.visible = false;
                this.$emit('onSubmit', {})
            },
            beforeClose(done){
             this.$emit('beforeClose', {})
             console.log('guanbi')
             done();
            }
        }
    }
</script>
