<template>
	<!-- id, -->
	<div :id="id" :style="style"></div>
</template>
<script>
export default {
	name: 'Echart',
	props: {
		// 
		outerData: { type: Array, default: () => [] },
		// 
		svcFunString: String,
		outParams: { type: Object, default: () => {} },
		// 
		optionFun: Function,
		id: {
			type: String,
			required: true,
		},
		width: { type: String, default: '100%' },
		height: { type: String, default: '100%' },
	},
	data() {
		return {
			// echarts
			theEcharts: '',
			// 
			baseOptions: {
				backgroundColor: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: 'rgba(255,255,255,0)', // 0% 
						},
						{
							offset: 1,
							color: 'rgba(20,140,240,.35)', // 100% 
						},
					],
					globalCoord: false, //  false
				},
			},
			options: {},
		}
	},
	computed: {
		style: function() {
			return {
				width: this.width,
				height: this.height,
			}
		},
	},
	watch: {
		// option
		//echarts
		options: {
			handler(newVal) {
				this.drawChart(newVal)
			},
			deep: true,
		},
	},
	mounted() {
		// 
		this.theEcharts = this.$echarts.init(document.getElementById(this.id))
		this.theEcharts.clear() //,
		// 
		this.initData()
	},
	methods: {
		// 
		initData() {
			// ,outerDatasvcFunString
			if (this.svcFunString === undefined) {
				this.options = this.optionFun(this.outerData)
			} else {
				// 
				this.handleFetch()
			}
		},
		drawChart(options) {
			this.theEcharts.setOption(
				Object.assign({}, this.baseOptions, options),
				true
			) //true
		},
		// 
		async handleFetch() {
			this.theEcharts.showLoading()
			const res = await this.$svc[this.svcFunString](this.outParams)
			this.theEcharts.hideLoading()
			if (!res) return // resundefined
			this.options = this.optionFun(res.data.data)
		},
	},
}
</script>
